import React from 'react';
import RealtimeSearch from "./RealtimeSearch";

const unit = {};

let optionSample = {
    type : 'package | watch | none',       // default watch
    target : '',                        // defualt undefined, calc-object, watch-string
    showIcon : '',                      // default true
    calcTag : '실제 아바타 값',
    calcTagImage : '',                  // default undefined
    defaultImage : '',                  // default undefined
    notiSoundType : ''
};

unit.Monster = () => {

    let monster = {
        title : 'MONSTER',
        type : 'package',
        target : {
            head : "몬스터 코스프레 패키지",
            body : ["몬스터 코스프레 칭호 상자", "몬스터 코스프레 오라 상자", "몬스터 코스프레 세라 상자"],
            tail : "몬스터 코스프레 아바타 풀세트 상자"
        },
        prefix : '몬스터 코스프레',
        notiSoundType: 0
    };

    let custom = {
        title : 'CUSTOM',
        type : 'watch',
        target : "퍼펙트 컨트롤 합성법",
        prefix : '몬스터 코스프레',
        notiSoundType: 1
    };

    return (
        <React.Fragment>
            <RealtimeSearch TITLE={monster.title} AVATAR={monster.target} OPTION={monster}/>
            <RealtimeSearch TITLE={custom.title} AVATAR={custom.target} OPTION={custom}/>
        </React.Fragment>
    )
};

