import React from 'react';
import socketIO from 'socket.io-client'
import '../styles/Quick.css';
import notiSound_1 from '../sounds/noti_1.mp3';
import notiSound_2 from '../sounds/noti_2.mp3';

function AddComma(str) {
    return Number(str).toLocaleString('en');
}

class RealtimeSearch extends React.Component {

    state = {
        //DATA
        snapshot : {},
        prevSnapshot : {},

        summary : {},
        prevSummary : {},

        cuttline : 6700000,
        timeStamp : 0,
        connect : 0,

        //CSS OPTION
        shown : false,
        color : '--white',
        innerWidth : window.innerWidth,

        //COMP OPTION
        tempAlramVar : 0,
        isWatching : true,

        //
        messageBox : []
    };

    socket = '';
    console_title = 1;

    componentDidMount () {

        window.addEventListener("resize",  () => {
            this.setState({innerWidth: window.innerWidth});
        }, false);

        this.console_title = [`%c@${this.props.TITLE}`, 'background: #222; color: #bada55'];
        console.log(...this.console_title, ` MOUNT`);
        //🔌

        if ( localStorage[this.props.TITLE] === undefined ) localStorage[this.props.TITLE] = 100000;
        else this.setState({cuttline : localStorage[this.props.TITLE]});

        let temp_url = window.location.origin.split(':');

        this.socket = socketIO.connect(temp_url[1], {transports: ['websocket']}, {'reconnectionAttempts': 5});
        const socket = this.socket;

        //console.log(socket.connected);

        socket.on('connect',  () => {
            console.log(...this.console_title, ` CONNECT`);
        });

        // TODO:SOCKET CONNECT() IS ASYNC FUNCTION. IF BELOW CODE IS REACHED WHEN BEFORE CONNECT IS ESTABILISHED
        // TODO:SOCKET IO INSERT THIS EMIT() IN QUEUE AND RUN AFTER ESTABILISHED?
        socket.emit(`getFirstData-${this.props.TITLE}`, '');

        socket.on('connect_failed', () => console.log(...this.console_title, ` CONNECT FAIL`));
        socket.on('reconnect_failed', () => console.log(...this.console_title, ` RECONNECT FAIL`));

        socket.on(`snapshot-${this.props.TITLE}`, async (received_snapshot, timeStamp) => {

            let recordTime = async (time) => await new Promise(res => setTimeout(res, time));
            if(!window.location.origin.includes('localhost') && Object.keys(this.state.prevSnapshot).length !== 0) await recordTime(6000);
            this.setState({color : '--green'}, () => {setTimeout(()=> this.setState({color:'--whitesmoke'}), 2000)});

            let snapshot = received_snapshot;
            let prevSnapshot = this.state.snapshot;
            let hash = this.hashCode(JSON.stringify(snapshot));
            let prevHash = this.hashCode(JSON.stringify(prevSnapshot));
            let isPrevSnapshotExist = Object.keys(this.state.prevSnapshot).length !== 0;

            // pre-processing #1 : re-sorting (for merged columss)
            Object.keys(snapshot).forEach((elem, idx) => {
                if (Array.isArray(snapshot[elem]) === true) {
                    snapshot[elem].sort(function (a, b) {
                        if (a.unitPrice > b.unitPrice) return 1;
                        if (a.unitPrice < b.unitPrice) return -1;
                        else return 0;
                    })
                }
            });
            //
            let lowest = Object.keys(snapshot).reduce((acc, cuv) => {

                if(snapshot[cuv].length === 0) acc[cuv] = 0;
                else acc[cuv] = snapshot[cuv][0].unitPrice;
                return acc;

            }, {});

            let prevLowest = Object.keys(prevSnapshot).reduce((acc, cuv) => {

                if(prevSnapshot[cuv].length === 0) acc[cuv] = 0;
                else acc[cuv] = prevSnapshot[cuv][0].unitPrice;
                return acc;
            }, {});

            let quantity = Object.keys(snapshot).reduce((acc, itemName) => {
                acc[itemName] = snapshot[itemName].reduce((quan, cuv) => {
                    quan += cuv.count;
                    return quan;
                },0);
                return acc;
            },{});

            let summary = {
                lowest : lowest,
                prevLowest : prevLowest,
                quantity : quantity,
                hash : hash
            };

            //

            if(!isPrevSnapshotExist) {

                let timeDiff = Math.abs(timeStamp - new Date().valueOf());
                if(timeDiff > 300000) this.pushMessage(`🙋 timeStamp recieved data is over 5minutes \ncheck worker${this.console_title[0]} state`);


                this.setState({
                    snapshot : snapshot,
                    prevSnapshot : snapshot,

                    summary : summary,
                    prevSummary : summary,

                    timeStamp : timeStamp,
                    hash : hash
                },  () => {});
            }
            // () => {}

            else {
                let prevSnapshot = this.state.snapshot;
                let prevSummary = this.state.summary;
                this.setState({
                    snapshot : snapshot,
                    prevSnapshot: prevSnapshot,

                    summary : summary,
                    prevSummary : prevSummary,

                    timeStamp : timeStamp,
                    hash : hash
                }, this.notiCallback);
            }
        });

    }

    componentWillUnmount = () => {
        console.log(`❎ UNMOUNT AND DISCONNECT`);
        this.socket.disconnect();
    };

    notiCallback = () => {

        let isNotify = false;

        switch (this.props.OPTION.type) {
            case 'package' : {
                if(typeof this.props.OPTION.target !== 'object') {
                    console.error('target is not Object, check props this component');
                    break;
                }
                let lowest = this.state.summary.lowest;
                let price_calculated = 1*(lowest[this.props.AVATAR.head]) - 0.97*(this.calculatePackage(lowest, this.props.AVATAR.body));
                let price_avatarFullSet = 1*(lowest[this.props.AVATAR.tail]);
                isNotify = ((price_calculated <= this.state.cuttline) || (price_avatarFullSet <= this.state.cuttline)) && (price_avatarFullSet !== 0) && this.state.tempAlramVar === 0;

                break;
            }
            case 'watch' : {
                if(typeof this.props.OPTION.target !== 'string') {
                    console.error('target is not String, check props this component');
                    break;
                }
                let lowest = this.state.summary.lowest;
                isNotify = ( 1*(lowest[this.props.OPTION.target]) <= 1*(this.state.cuttline) && this.state.tempAlramVar === 0 && 1*lowest[this.props.OPTION.target] !== 0);
                //console.log(1*(lowest[this.props.OPTION.target]), 1*(this.state.cuttline), this.state.tempAlramVar  );
                break;
            }
        }

        //console.log(isNotify);

        if(isNotify) {
            let callback = () => {
                setTimeout(() => this.setState({tempAlramVar: 0}), 15000);
            };
            this.setState({tempAlramVar: 1},callback);

            //new Notification('알림', { body: `${this.hashConvert(this.state.hash)}＠${Math.floor(calcValue)}` });

            let audioEl = document.getElementsByClassName("audio-element")[(this.props.OPTION.notiSoundType || 0)];
            audioEl.volume = 0.7;
            audioEl.play();
            console.log(...this.console_title, ` NOTIFICATION at ${ new Date().toLocaleString() }`);

        }
    };

    hashCode = (s) => {
        return s.split("").reduce(function(a,b){a=((a<<5)-a)+b.charCodeAt(0);return a&a},0);
    };

    hashConvert = (num) => {
        return String(Math.abs(num)).split('').map((elem)=> 1*elem).reduce((acc,cuv) => (acc += String.fromCharCode(97+cuv), acc),'');
    };

    signString = (prev, curr) => {
        let diff = curr - prev;
        let sign = Math.sign(diff) + 0;
        if(sign === 0)  return ['--zero', '', '-'];
        if(sign === 1)  return ['--plus', '▲ ', Math.abs(diff).toLocaleString('en')];
        if(sign === -1) return ['--minus', '▼ ', Math.abs(diff).toLocaleString('en')];

    };
    toggle = (e) => {
        console.log(e.target.classList);
        let elem = document.querySelector(`#${e.target.getAttribute('id')}`);
        if(elem.style.display === 'none'){
            elem.style.display = 'flex';
        } else {
            elem.style.display = 'none';
        }
    };

    isOverCuttLine = (price) => {
        return !!price === false ? false : Math.sign(price - this.state.cuttline) < 1;
    };

    watchToggle = () => {

    };

    inputVerify = (e) => {
        let numChuck = Number.isInteger(1*e.target.value);
        if(numChuck) {
            localStorage[this.props.TITLE] = 1*e.target.value;
            return this.setState({cuttline: Number(e.target.value)});
        }
    };

    ff_popupWindow = () => {
        window.open(window.location.href, '', 'width=480, height=480');
    };
    ff_showDetails = () => {
        alert(JSON.stringify(this.state.snapshot));
    };
    showAllitem = (itemName) => {
        let prompt = '';
        prompt = this.state.snapshot[itemName].reduce((acc, cuv, idx) => {
            let spaceingSize = 5 - cuv.count.toString().length;
            if(spaceingSize < 0) spaceingSize = 0;
            acc += `${ this.itemNameSp(cuv.itemName) } [${cuv.count}] ${(" ").repeat(spaceingSize)} || ${"  "} ${AddComma(cuv.unitPrice)} \n`;
            return acc;
        }, '');
        alert(prompt);
    };

    calculatePackage = (lowest, body) => {
        if(Object.keys(lowest).length === 0) return 0;
        if(!Array.isArray(body) || this.props.OPTION.type !== 'package') return 0;
        let sumOfBody = body.reduce((acc, cuv) => {
            if(lowest[cuv] === undefined) {
                //console.warn(`check member varible of AVATAR`);
            }
            acc += lowest[cuv];
            return acc;
        }, 0);
        return sumOfBody;
    };

    itemNameSp = (itemObject) => {

        if( typeof itemObject === 'object') {
            let itemName = itemObject.itemName;
            if(itemObject.itemType === '방어구' || itemObject.itemType === '무기') itemName += ` [+${itemObject.reinforce}]`;
            itemName = itemName.replace(new RegExp(this.props.OPTION.prefix, 'g'), '');
            if(itemName.includes('아바타 풀세트')) itemName = itemName.replace('풀세트 상자', '');
            return itemName;
        }
        else {
            let itemName = itemObject;
            itemName = itemName.replace(new RegExp(this.props.OPTION.prefix, 'g'), '');
            if(itemName.includes('아바타 풀세트')) itemName = itemName.replace('풀세트 상자', '');
            return itemName;
        }

    };

    downloadState = () => {
        var filename = `rawData_${this.props.TITLE}_${new Date().toLocaleString().replace(/:/g, '\`')}.json`;
        var a = document.createElement('a');
        var blob = new Blob([JSON.stringify(this.state)]);
        a.download = filename;
        a.href = URL.createObjectURL(blob);
        a.click();
        URL.revokeObjectURL(blob);
    };

    pushMessage = (message) => {
        console.log(this.state.messageBox);
        this.setState({messageBox : [...this.state.messageBox, {message : message, timeStamp : Date.now()}]},
            () => {
            setTimeout( ()=> {
                let m = this.state.messageBox;
                m.splice(0,1);
                this.setState({messageBox : m});}, 3000);
            });
    };

    copy = (param) => {
        navigator.clipboard.readText().then((text) => {
            //console.log('Pasted content: ', text);
        });
        let lowest = this.state.summary.lowest;
        let str = '';
        str += new Date(this.state.timeStamp).toLocaleTimeString();
        str += '\t';
        let space;
        if(param === 'vertical') space = '\n';
        else space = '\t';
        let len = Object.keys(lowest).length;
        let a = Object.keys(lowest).forEach((elem, idx) => {
            if(idx !== len -1) str += lowest[elem] + space;
            else  str += lowest[elem];

        });
        navigator.clipboard.writeText(str);
        this.pushMessage('✔️  copied !');
    };

    keyEvent_quick = (e) => {
        //console.log(key);
        if(e.key === 'c' || e.key === 'C') { this.copy() }

    };

    render() {
        let snapshot = this.state.snapshot;
        let lowest = this.state.summary.lowest || {};
        let prevLowest = this.state.prevSummary.lowest || {};
        let quantity = this.state.summary.quantity || {};


        let pTarget = this.props.OPTION.target;
        let pType = this.props.OPTION.type;

        let attributeRow;
        let indexRow;
        let additionalRow;
        let underTable;
        let testElement;

        //attributeRow
        attributeRow = <div className={"attribute row --shown-"+this.state.shown} onClick={() => this.setState({shown: !this.state.shown})}>
            <div className={"data cell --super-short"}> </div>
            <div className={"attribute cell --long"}> 검색어 </div>
            <div className={"attribute cell --short"}> 가격 </div>
            <div className={"attribute cell --short"}> 변동폭 </div>

            {this.state.innerWidth >= 490 ? <div className={"attribute cell --extra-short"}> 물량 </div> : null}
        </div>;

        //indexRow
        indexRow = '';

        //addtionalRow, with calculating
        if(pType === 'package') {
            let sumValue_temp = 0.97*(this.calculatePackage(lowest, this.props.AVATAR.body));
            let price_calculated = 1*(lowest[this.props.AVATAR.head]) - 0.97*(this.calculatePackage(lowest, this.props.AVATAR.body));
            let didOverCuttLine = this.isOverCuttLine(price_calculated);

            additionalRow = <div className={"data row "+(didOverCuttLine ? '--red' : '--none')}>
                <div className={"data cell --super-short"}><img src={ null }  /></div>
                <div className={"data cell --long"}> { "실제 아바타값" } </div>
                <div className={"data cell --short --num "} title={Math.floor(sumValue_temp)}> { Math.floor(price_calculated || 0).toLocaleString('en') || 0 } </div>
                <div className={"data cell --short"}> {  } </div>
                {this.state.innerWidth >= 490 ? <div className={"data cell --extra-short"}> {  } </div> : null }
            </div>;
        }

        //underTable ;;;type === 'package' || type === 'watch'
        if(true) {
            let typeFlag = pType === 'watch' || pType === 'package';
            underTable =
                <div className={"lowest-tail --flex"}>
                <div className={"--flex --space-between"}>
                    <div className={"receive-time --flex"}>
                        <div className={"--pointer"} onClick={this.downloadState}><i className={"fas fa-caret-square-down "+this.state.color+" --temp"}></i></div>
                        <div> { new Date(this.state.timeStamp || 0).toLocaleTimeString() } </div>
                    </div>
                    {/*<div onClick={this.copy.bind(this, 'vertical')} style={{cursor: "pointer"}}> { '📋v' } </div>*/}
                    <div onClick={this.copy.bind(this, 'horizon')} style={{cursor: "pointer"}}> { '📋' } </div>
                    {/*this.hashConvert(this.state.hash)*/}
                </div>
                {
                    <div className={"--flex --relative " + ( typeFlag ? '' : '--unable')}>
                        { typeFlag ? <i className="far fa-eye --inner-eye" onClick={this.watchToggle}></i> : <i className="far fa-eye-slash --inner-eye"></i>}
                        <input className={"--simpleInput"} id={`autoComplete2-`+this.props.TITLE} value={this.state.cuttline} onChange={this.inputVerify}/>
                    </div>
                }
            </div>;
        }

        testElement = <div onClick={this.copy}>📙</div>;

        return (
            <div id={"quick"}  tabIndex={0} onKeyPress={this.keyEvent_quick}>

                <div className={"float-message-box"}>
                    {
                        this.state.messageBox.map((elem, idx) => {
                            return (
                                <div className={`floating-modal` } key={elem.timeStamp}> {elem.message} </div>
                            )
                        })
                    }
                </div>


                <audio className={"audio-element"}>
                    <source src={notiSound_1}/>
                </audio>
                <audio className={"audio-element"}>
                    <source src={notiSound_2}/>
                </audio>
                <div className={"wrapper"}>


                    <div className={"lowest"}>
                        {attributeRow}
                        {
                            Object.keys(lowest).map((itemName, idx) => {
                                let price_typeNumber = lowest[itemName] ;
                                let price = AddComma(lowest[itemName]);
                                let sign = this.signString(prevLowest[itemName], lowest[itemName]);
                                let isTarget;

                                if(this.props.OPTION.type === 'package') isTarget = itemName.includes(this.props.AVATAR.tail);
                                if(this.props.OPTION.type === 'watch') isTarget = itemName.includes(this.props.OPTION.target);

                                return(

                                    <div className={"data row " +(this.isOverCuttLine(price_typeNumber)&&isTarget ? '--red' : '--none')} key={idx} >
                                        <div className={"data cell --super-short"}><img src={ snapshot[itemName][0] ? `https://img-api.neople.co.kr/df/items/${(snapshot[itemName][0] || {}).itemId}` : null }  /></div>
                                        {/*<div className={"data cell --long --elipsis"}> { Object.keys(snapshot[itemName]).length >= 1 ? snapshot[itemName][0].itemName : itemName } </div>*/}
                                        <div className={"data cell --long --elipsis"} title={itemName}> { Object.keys(snapshot[itemName]).length >= 1 ? this.itemNameSp(snapshot[itemName][0]) : this.itemNameSp(itemName) } </div>
                                        <div className={"data cell --short --num --hover-bold --pointer"} onClick={() => this.showAllitem(itemName)}> <div className={"--hover-mint"}>{ price !== '0' ? price : '×' }</div> </div>
                                        <div className={"data cell --short --elipsis --num "+sign[0]}> {sign[1]}{ sign[2] } </div>
                                        {this.state.innerWidth >= 490 ? <div className={"data cell --extra-short --num "}> { quantity[itemName] } </div> : null }
                                    </div>

                                )
                            })
                        }
                        {additionalRow}
                        {underTable}

                    </div>
                    <div className={"snapshot"}></div>
                </div>
            </div>
        )
    }
}

RealtimeSearch.defaultProps  = { OPTION : {}};

export default RealtimeSearch;